// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css';
import axios from 'axios'
import NProgress from 'NProgress'
import 'nprogress/nprogress.css'
import store from './store/store';
import * as types from './store/types'
import md5 from 'js-md5';
import global from './components/util/Global'

import hzqingVueTimeline from 'hzqing-vue-timeline'


import 'gitment/style/default.css'
import Gitment from 'gitment'


Vue.use(hzqingVueTimeline)

Vue.prototype.GLOBAL = global
Vue.prototype.$md5 = md5;
NProgress.configure({ easing: 'ease', speed: 2000 });
Vue.prototype.$ajax = axios
Vue.use(ElementUI);
Vue.config.productionTip = false

// axios.defaults.baseURL = '/api'

// 
axios.interceptors.response.use(
  response => {
    return response
  },
  err => {
    if (err && err.response) {
      switch (err.response.status) {
        case 400:
          err.message = ''
          break
        case 401:
          err.message = ''
          break
        case 403:
          err.message = ''
          break
        case 404:
          err.message = ','
          break
        case 405:
          err.message = ''
          break
        case 408:
          err.message = ''
          break
        case 500:
          err.message = ''
          console.log(err.response)
          if (
            err.response.data.message === 'Header' ||
            err.response.data.message === '!'
          ) {
            store.commit(types.LOGOUT)
            router.replace({
              path: '/user/login',
              query: { redirect: router.currentRoute.fullPath }
            })
          }
          break
        case 501:
          err.message = ''
          break
        case 502:
          err.message = ''
          break
        case 503:
          err.message = ''
          break
        case 504:
          err.message = ''
          break
        case 505:
          err.message = 'http'
          break
        default:
          err.message = `${err.response.status}`
      }
    } else {
      err.message = ''
    }
    // message.err(err.message)
    return Promise.resolve(err.response)
  }
)


// 
axios.interceptors.request.use(
  config => {
    // 
    return config
  },
  error => {
    return Promise.reject(error)
  }
)

// token
if (window.localStorage.getItem('token')) {
  console.log("flush")
  store.commit(types.LOGIN, window.localStorage.getItem('token'))
}

// http request 
axios.interceptors.request.use(
  config => {
    if (store.state.token) {
      config.headers.Authorization = `${store.state.token}`
    }
    return config
  },
  err => {
    return Promise.reject(err)
  }
)


router.beforeEach((to, from, next) => {
  NProgress.start();
  NProgress.set(1)
  NProgress.inc()
  next()
});

router.afterEach(transition => {
  //
  NProgress.done()
});



/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



